import React, { Suspense } from "react";
import { Route, Routes } from "react-router-dom";

function App() {

  const LandingComponent = React.lazy(() => import("./components/LandingPage/LandingPage"));
  const Landingv2Component = React.lazy(() => import("./components/LandingPage/LandingPage2"));



  const RouteConponents = [
    { path: "/", element: <LandingComponent/>},
    { path: "/v2", element: <Landingv2Component /> },
  ];
  
  return (
    <div style={{backgroundColor:'#041C25'}}>
    <Suspense fallback={<h1>Loding</h1>}>
        <Routes>
          {RouteConponents.map((props) => (
            <Route path={props.path} element={props.element} />
          ))}
        </Routes>
    </Suspense>
    </div>
  );
}


export default App;
